import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { NavbarComponent } from './common/navbar/navbar.component';
import { FooterComponent } from './common/footer/footer.component';
import { MenuAccueilComponent } from './common/menu-accueil/menu-accueil.component';
import { QuiSommeNousComponent } from './common/qui-somme-nous/qui-somme-nous.component';
import { AccueilComponent } from './common/accueil/accueil.component';
import { AfrovibesComponent } from './common/afrovibes/afrovibes.component';

const appRoutes: Routes = [
  { path: 'afrovibes', component: AfrovibesComponent },
  { path: 'qui-sommes-nous', component: QuiSommeNousComponent},
  { path: 'home', component: AccueilComponent },
 /* { path: '', component:  },*/
];
@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    FooterComponent,
    MenuAccueilComponent,
    QuiSommeNousComponent,
    AccueilComponent,
    AfrovibesComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



